.grid__main table.gridjs-table tbody tr:hover {
  background: #f4f4f4;
  cursor: pointer;
}

.content.grid__main section.grid__main__search {
  grid-column: span 4 / span 4;
}

.content.grid__main section.grid__main__search .search__wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
}

.content.grid__main section.grid__main__search .search__wrap .input__wrap > dl {
  width: 49%;
}

.content.grid__main section.grid__main__search .search__wrap > .input__wrap {
  width: 50%;
}

.content.grid__main section.grid__main__search .search__wrap .input__wrap input {
  width: 100%;
}

.content.grid__main section.grid__main__search .search__wrap dl {
  margin-right: 24px;
}

.content.grid__main section.grid__main__search .search__wrap dl:last-of-type {
  margin-right: 0px;
}

.content.grid__main section.grid__main__search .search__wrap dt {
  margin-bottom: 12px;
}

.content.grid__main section.grid__main__search .search__wrap > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 18px;
}

.content.grid__main section.grid__main__search .search__wrap .selector__wrap dd {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.content.grid__main
  section.grid__main__search
  .selector__wrap
  .react-datepicker-wrapper:first-of-type {
  margin-right: 16px;
}

.content.grid__main table.gridjs-table {
  width: 100%;
  min-width: 2600px;
}

.content.disability__history table.gridjs-table {
  min-width: 1700px;
}

@media (max-width: 1200px) {
  .content.grid__main section.grid__main__search .search__wrap .input__wrap input {
    min-width: 180px;
  }
}

@media (max-width: 1040px) {
  .content.grid__main section.grid__main__search .search__wrap > div {
    margin-right: 0px;
  }

  .content.grid__main section.grid__main__search .search__wrap {
    flex-direction: column;
  }

  .content.grid__main section.grid__main__search .search__wrap .input__wrap {
    width: 100%;
  }

  .content.grid__main section.grid__main__search .search__wrap > div {
    width: 100%;
    margin-bottom: 24px;
  }

  .content.grid__main section.grid__main__search .search__wrap .input__wrap dl {
    width: 49%;
  }

  .content.grid__main section.grid__main__search .search__wrap > button {
    width: 100%;
  }

  .content.grid__main section.grid__main__search .search__wrap .selector__wrap {
    justify-content: flex-start;
  }
}

@media (max-width: 650px) {
  .content.grid__main section.grid__main__search .search__wrap .selector__wrap dd {
    flex-direction: column;
  }

  .content.grid__main section.grid__main__search .search__wrap .selector__wrap {
    flex-direction: column;
  }

  .content.grid__main section.grid__main__search .search__wrap .selector__wrap dl {
    margin-right: 0px;
    margin-bottom: 12px;
    width: 100%;
  }

  .content.grid__main
    section.grid__main__search
    .selector__wrap
    .react-datepicker-wrapper:first-of-type {
    margin-right: 0px;
    margin-bottom: 12px;
  }

  .react-datepicker__input-container > input {
    max-width: none;
    width: 100%;
  }

  .content.grid__main section.grid__main__search .selector__wrap .react-datepicker-wrapper {
    width: 100%;
  }

  .content.grid__main section.grid__main__search .search__wrap .selector__wrap dl dd {
    width: 100%;
  }

  .content.grid__main section.grid__main__search .selector__wrap .custom__select {
    width: 100%;
  }

  .content.grid__main section.grid__main__search .search__wrap > .input__wrap {
    flex-direction: column;
  }

  .content.grid__main section.grid__main__search .search__wrap .input__wrap dl {
    margin-right: 0px;
    width: 100%;
  }

  .content.grid__main section.grid__main__search .search__wrap .input__wrap dl:first-of-type {
    margin-bottom: 12px;
  }

  .content.grid__main section.grid__main__search .search__wrap > div {
    margin-bottom: 12px;
  }
}
