.section__grid__wrap.device__detail__history {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.content.section__grid__wrap section.device__detail__history {
  grid-column: span 4 / span 4;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 24px;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left {
  width: calc(100% - 420px);
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .right {
  width: 400px;
  display: flex;
  justify-content: space-between;
  min-height: 148.4px;
  align-items: flex-end;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div {
  min-height: 148.4px;
  width: calc(100% - 50px);
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div>dl {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-height: 148.4px;
  width: calc(100% - 50px);
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div>dl>dd {
  border: 1px solid rgb(226, 226, 226);
  box-sizing: border-box;
  border-radius: 8px;
  max-height: 102.4px;
  overflow-y: auto;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div>dl>dd li {
  padding: 7px 12px;
  box-sizing: border-box;
  cursor: pointer;
  border-bottom: 1px solid rgb(235, 235, 235);
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div>dl>dd li.active {
  background: var(--main-color);
  color: white;
  border-bottom: 1px solid rgb(244, 244, 244);
  box-sizing: border-box;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dd>ul {
  display: flex;
  justify-content: space-between;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dt {
  margin-bottom: 12px;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl {
  margin-bottom: 24px;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dd li {
  width: 22%;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl:first-of-type dd li select {
  width: 100%;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl:last-of-type {
  margin-bottom: 0px;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl:last-of-type dd li>div {
  height: 100%;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl:last-of-type dd li>div>div {
  height: 100%;
}

.content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl:last-of-type dd li>div>div>input {
  height: 100%;
  max-width: none;
  width: 100%;
}

@media (max-width: 1150px) {
  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dd>ul {
    flex-wrap: wrap;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dd li {
    width: 48%;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl:last-of-type dd li>div {
    width: 100%;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dd li:first-of-type,
  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dd li:nth-of-type(2) {
    margin-bottom: 12px;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div>dl {
    width: calc(100% - 20px);
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right {
    min-height: 260px;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div {
    min-height: 260px;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div>dl {
    min-height: 260px;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div>dl>dd {
    min-height: 240px;
  }
}

@media (max-width: 850px) {
  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left {
    width: calc(100% - 320px);
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right {
    width: 300px;
  }
}

@media (max-width: 700px) {
  .content.device__detail__history .device__detail__history>.section__inner .search__wrap {
    flex-direction: column;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left {
    width: 100%;
    margin-bottom: 24px;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right {
    width: 100%;
  }
}

@media (max-width: 500px) {
  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dd li {
    width: 100%;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .left>div dl dd li:nth-of-type(3) {
    margin-bottom: 12px;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right {
    flex-direction: column;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div {
    width: 100%;
    margin-bottom: 12px;
  }

  .content.device__detail__history .device__detail__history>.section__inner .search__wrap .right>div>dl {
    width: 100%;
  }
}

.device__detail__history .chart__wrap {
  max-height: 400px;
}